body {
  background-color: #fcfcfc;
  /* --- select group message --- */ }
  body input:focus {
    outline: none !important;
    border: 1px solid #ff2e27 !important; }
  body .modal-title {
    float: left; }
  body .btn:focus {
    outline: none !important; }
  body .btn i {
    font-size: 1.2rem; }
  body .btn.btn-fab {
    border-radius: 50%;
    position: fixed;
    right: 30px;
    bottom: 30px;
    width: 60px;
    height: 60px;
    z-index: 2; }
  body .select-group-message {
    display: none; }
    body .select-group-message .card {
      margin: 0 auto;
      text-align: center;
      width: 25rem; }
  body.loaded .select-group-message {
    display: block; }
  body.group-selected .select-group-message {
    display: none; }
  body.group-selected .btn.new {
    display: block !important; }

.navbar {
  background-color: #fff;
  border: 1px solid #ddd; }
  .navbar a.navbar-brand {
    padding-top: 0.75rem; }
    .navbar a.navbar-brand img {
      width: 100px; }
  .navbar .form-inline {
    display: inline-block;
    max-width: 300px; }
  .navbar .navbar-collapse {
    display: block;
    float: right;
    width: 408px; }
  .navbar .btn.user {
    margin-left: 0.4rem; }
  .navbar .dropdown .dropdown-menu {
    right: 0;
    left: auto;
    top: 46px;
    margin-left: 1rem; }
  .navbar .avatar {
    width: 18px;
    border-radius: 50%;
    margin: 0.3rem 0.1rem 0.4rem 0.1rem; }
  .navbar .profile {
    padding: 0.4rem 1.6rem; }
    .navbar .profile textarea {
      border: 1px solid #efefef;
      width: 220px;
      border-radius: 5px;
      margin: 0px;
      font-family: monospace;
      font-size: small; }
  .navbar .btn {
    float: right; }
  .navbar .btn.logout {
    margin-left: 10px; }
  .navbar .btn.save,
  .navbar .btn.logout {
    display: none; }
  .navbar .btn.download,
  .navbar .btn.save {
    margin-right: 10px; }

body.loaded .btn.save,
body.loaded .btn.logout,
body.loaded .btn.download,
body.loaded .btn.new {
  display: block; }

body:not(.loaded) .user {
  display: none; }

@media (max-width: 768px) {
  .navbar .navbar-collapse {
    max-width: 200px;
    margin-bottom: 6px; }
  .navbar .form-inline {
    display: block;
    max-width: 100%; } }

.loader {
  display: block;
  opacity: 0;
  position: absolute;
  left: 50%;
  top: -50px;
  margin-left: -20px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  width: 40px; }
  .loader img {
    width: 100%; }

.load-overlay {
  display: block;
  position: fixed;
  top: 56px;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 88888; }
  .load-overlay .btn {
    opacity: 1;
    position: absolute;
    transition: opacity 0.7s ease;
    left: 50%;
    margin-left: -117px;
    margin-top: 100px; }
    .load-overlay .btn img {
      width: 200px; }

body.loading .loader {
  opacity: 1;
  top: 14px; }

body.loading .load-overlay .btn {
  opacity: 0; }

body.loaded .load-overlay {
  display: none; }

.table {
  margin-top: 65px;
  opacity: 0.4;
  transition: all 0.5s ease;
  /* menu buttons */ }
  .table .controls {
    border-bottom: 1px solid #efefef;
    display: block;
    min-height: 50px; }
    .table .controls .dropdown {
      float: left; }
    .table .controls .btn.download {
      display: inline-block; }
  .table .row {
    padding: 10px 0 0;
    margin: 0;
    /*min-width: 1000px;*/ }
    .table .row .cell {
      float: left;
      padding: 0 6px;
      position: relative;
      width: calc(25% - 20px); }
  .table .row > .cell:nth-child(5) {
    width: 80px;
    margin-top: 3px;
    padding: 0; }
    .table .row > .cell:nth-child(5) .btn {
      padding: .1rem .4rem 0rem .4rem; }
    .table .row > .cell:nth-child(5) .btn:first-child {
      margin-left: 0.4rem; }
  .table .thead {
    display: none; }
    .table .thead .cell {
      font-weight: 600;
      letter-spacing: 0.2rem;
      text-transform: uppercase;
      font-size: 0.75rem;
      padding-top: 5px; }
    .table .thead .sort span {
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      height: 0;
      margin-bottom: 3px;
      margin-left: 5px;
      width: 0; }
      .table .thead .sort span.darr {
        display: none;
        border-top: 5px solid #333; }
      .table .thead .sort span.uarr {
        display: inline-block;
        border-bottom: 5px solid #333; }
    .table .thead .sort.desc span.darr {
      display: inline-block; }
    .table .thead .sort.desc span.uarr {
      display: none; }
    .table .thead .sort:hover {
      cursor: pointer; }
  .table .tbody .row {
    height: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0;
    transition: opacity 0.4s ease; }
  .table .tbody .row.fadein {
    height: auto;
    opacity: 1;
    padding: 10px 0 0; }
  .table .tbody .row.child {
    border-left: 1px solid #ff2e27;
    float: right;
    margin-right: -20px;
    width: 100%; }
  .table .tbody .row.child > .cell:first-child {
    width: calc(25% - 40px); }
  .table .tbody input {
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    color: #55595c;
    display: inline-block;
    font-size: 1rem;
    line-height: 1.6;
    width: 100%; }
  .table .tbody input[disabled] {
    border-color: transparent;
    background-color: #eee; }

/* body class conditionals */
.loaded .table {
  opacity: 1; }

.group-selected .thead {
  display: block; }

/* mobile */
@media (max-width: 1000px) {
  .table {
    margin-top: 108px; }
    .table .thead {
      display: none; }
    .table .row .cell,
    .table .row .cell:nth-child(5) {
      margin: 10px 0;
      padding-right: 8px;
      width: 100%; }
    .table .row .menu .btn {
      float: none; }
    .table .row .menu.active {
      left: auto;
      right: 20px; }
    .table .tbody .row.child {
      width: 97%;
      margin-right: 0; }
    .table .tbody .row.child > .cell:last-child,
    .table .tbody .row.child > .cell:first-child {
      width: 100%; } }

.dropdown {
  position: relative; }
  .dropdown .dropdown-menu {
    left: 0; }
    .dropdown .dropdown-menu li {
      padding: 3px; }
      .dropdown .dropdown-menu li a {
        color: #ff2e27;
        display: block;
        padding: 3px 20px;
        clear: both;
        font-weight: 400;
        line-height: 1.42857143;
        white-space: nowrap; }
      .dropdown .dropdown-menu li a:hover {
        color: #55595c;
        text-decoration: none;
        background-color: #f5f5f5; }
  .dropdown .js-add-locker span {
    display: none; }
  .dropdown .js-add-locker.btn-secondary > span:first-child {
    display: block; }
  .dropdown .js-add-locker.btn-success > span:last-child {
    display: block; }
  .dropdown input {
    line-height: 1.6;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    transition: all 0.3s ease;
    width: 0; }
  .dropdown input.active {
    opacity: 1;
    width: calc(100% - 50px); }

.generator {
  float: right;
  position: relative; }
  .generator div, .generator .btn {
    display: inline-block; }
  .generator input {
    position: absolute;
    right: 50px;
    width: 140px; }
